.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.colorMenu{
  background-color: #383430;
}
.colorBody{
  background-color: rgb(255, 255, 255);
  width: 100%;
  height: 100%;
}
.titulo{
  background-color:#8f8f8f ;
  height: 140px;
}
.apartados{
  background-color:#46413c ;
  border: solid rgb(255, 255, 255) 10px;
  border-radius: 30px;
  height: 140px;
}
.nombreApartados{
  color: white;
  padding-top: 40px;
}
.imagenesCards{
  height: 15rem;
}
.infoCard{
  height: 17rem;
}
footer{
  width: 96%;
  margin-left:1% ;
  margin-right:1% ;
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  border: solid gray 2px;
  border-radius: 10px 10px 0px 0px;
  background: white;
}
.cardsImagenes{
  padding: 2px;
  float: left;
}
.textoPieDePagina{
  float: left;
  position: relative;
  margin-left: 5%;
}
